import { Link } from 'react-router-dom'
import './Navbar.css'

export default function Navbar(){
    return (
    <nav className="navbar fixed-top navbar-expand-sm bg-light my-nav">
    <div className="container-fluid p-2">
        <Link className="navbar-brand" to="/"><h4 className='my-0'>Photo Galari</h4></Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item">
                    <Link to="/public" className='nav-link textd-none'>Public</Link>
                </li>
                <li className="nav-item">
                    <Link to="/Local" className='nav-link textd-none'>Local</Link>
                </li>
            </ul>
            <Link to="/add-post" className='add-post-btn rounded text-white py-1 px-3 textd-none me-3'>+ Post</Link>
        </div>
    </div>
    </nav>
    )
}